.input-group-text {
    border-radius: 0;
}

.loan-label{
    margin-bottom: 0;
}
.dd-wrapper {
    width: 100%;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
/*     -webkit-box-shadow: 3px 2px 4px 0px rgba(0,0,0,.06);
       -moz-box-shadow: 3px 2px 4px 0px rgba(0,0,0,.06);
            box-shadow: 3px 2px 4px 0px rgba(0,0,0,.06); */
}

.dd-header {
    width: 100%;
    position: relative;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: space-between;
    line-height: 38px;
    background-color: #fff;
    /* border: 1px solid #dfdfdf; */
    border: 1px solid #96e1fa;
}

.dd-header i{
    font-size: 27px;
    margin-right: 10px;
}

.dd-header-title {
    margin: 0 12px;
    font-size: 17px;
    font-weight: 400;
}

.angle-down {
    color: #16181b;
    margin-right: 20px;
}

.dd-list {
    width: 100%;
    margin: 0;
    padding: 0;
    border: 1px solid #dfdfdf;
    background-color: #fff;
    box-shadow: 0 2px 5px -1px #e8e8e8;
}

.dd-list-item {
    width: 100%;
    color: #16181b;
    font-weight: 400;
    font-size: 16px;
    padding: 8px 10px;
    cursor: pointer;
    list-style: none;
}

.dd-header-title span small,
.dd-list-item small {
    color: #176d2b;
    font-weight: 500;
}

.dd-list-item:hover {
    background-color: #f2f8ff;
}

.dd-list-item.selected,
.dd-list-item.selected small,
.dd-list-item.selected:hover,
.dd-list-item.selected:hover small{
    color: #fff;
    background-color: #287dad;
}

.dd-header i {
    position: absolute;
    right: 5px;
    padding: 5px;
    display: inline-block;
    border: solid #909090;
    border-width: 0 3px 3px 0;
}

.dd-header i.up{
    top: 17px;
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.dd-header i.down {
    top: 10px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

/**
 * This file contains Calculator's general style
 */
 .mortgage-prequalifying-calculator-wrapper .h2, .mortgage-prequalifying-calculator-wrapper h2 {
  font-size: 1.7rem;
}

.mortgage-prequalifying-calculator-wrapper .calculatorTooltip{
  width: 220px;
  opacity: 1 !important;
}

.mortgage-prequalifying-calculator-wrapper .helpMiniDisk{
  height: 22px;
  width: 22px;
  font-size: 17px;
  text-align: center;
  padding: 0;
  border: 0;
  color: white;
  background-color: #2a353d;
  box-shadow: none;
  text-shadow: none;
  border-radius: 50%;
}

.mortgage-prequalifying-calculator-wrapper .shadowBox{
  width: 100%;
  border: 1px solid #eef2f7;
  box-shadow: 3px 2px 7px 0 rgba(0,0,0,.15);
}

.mortgage-prequalifying-calculator-wrapper .inputError{
  border: 1px solid #dc3545;
}

.mortgage-prequalifying-calculator-wrapper .inputErrorText{
  color: #dc3545;
}

.mortgage-prequalifying-calculator-wrapper .inputIconError{
  color: #ffffff;
  background: #dc3545;
  border: 1px solid #dc3545;
}

.mortgage-prequalifying-calculator-wrapper .show{
  display: block;
}
.mortgage-prequalifying-calculator-wrapper .hide{
  display: none;
}

.mortgage-prequalifying-calculator-wrapper button:disabled,
.mortgage-prequalifying-calculator-wrapper button[disabled]{
    border: 1px solid #999999 !important;
    background-color: #cccccc !important;
    color: #666666 !important;
}

.mortgage-prequalifying-calculator-wrapper h4{
  font-size: 18px;
  font-weight: 400;
}

.mortgage-prequalifying-calculator-wrapper .number-populated{
  width: 100%;
  font-size: 1.8em;
  color: #000000;
  font-weight: 400;
  text-align: center;
  background: #ffffff;
  border-radius: 4px;
  padding: 15px 0;
  margin-bottom: 0.5rem;
}

.mortgage-prequalifying-calculator-wrapper .tooltipContainer{
  display: inline-block;
  position:relative;
}
.mortgage-prequalifying-calculator-wrapper .customTooltip{
  position: absolute;
  bottom: 135%;
  left: -18px;
  min-width: 260px;
  max-width: 300px;
  border-radius: 3px;
  font-size: 13px;
  padding: 10px 15px;
  z-index: 999;
  color: #ffffff;
  background-color: #222222;
}

.mortgage-prequalifying-calculator-wrapper .customTooltip:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 22px;
  width: 0; height: 0;
  border-top: 8px solid #222222;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
}

.mortgage-prequalifying-calculator-wrapper .graph-container{
  border: 1px solid #ededed;
  border-radius: 4px;
  padding: 25px 35px 15px;
}

.loan-term-label-wrap{
    padding-left: 30px !important;
}
.loan-term-label-wrap .tooltipContainer{
    position: absolute;
    left: 0;
}

.p-relative{
